import { Navbar } from "@/components/navbar";
import { getDashboardData } from "./actions";
import { UsernameCard } from "./_components/username-card";
import { ApplicationCard } from "./_components/application-card";
import { redirect } from "next/navigation";

export default async function DashboardPage() {
  const data = await getDashboardData();

  if (!data) {
    redirect("/sign-in");
  }

  const { user, application } = data;

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />
      <main className="flex-1 space-y-8 p-8 pt-6">
        <div className="flex items-center justify-between space-y-2">
          <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
        </div>
        
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
          <div className="col-span-4">
            <UsernameCard initialUsername={user.minecraftUsername} />
          </div>
          <div className="col-span-3">
            <ApplicationCard 
              status={application?.status || null} 
              createdAt={application?.createdAt}
              reviewedAt={application?.reviewedAt}
            />
          </div>
        </div>
      </main>
    </div>
  );
}